.page-id-181 .sg-title {
    width: 100%;
    color: $white;
    padding: 20px;
    margin: 0 0 10px;
}
.page-id-181 .sg-text {
    width: 50%;
    clear: both;
    @mixing clear;
}
.page-id-181 .sg-text a {
    font-weight: 600;
    font-family: 'Merriweather';
    text-decoration: underline;
    display: block;
    color: $blue;
}
.page-id-181 .sg-text a:hover {
    color: $blue_light;
}
.page-id-181 .sg-text a.purple {
    color: $purple;
}
.page-id-181 .sg-text a.purple:hover {
    color: #aa5f9a;
}
.page-id-181 h1.sg-title {
    background: $gray;
    font-size: 20px;
}
.page-id-181 h2.sg-title {
    background: $light_gray;
    color: $blue;
    font-size: 16px;
}
.page-id-181 h3.sg-title {
    background: $blue_light;
}
.page-id-181 .sg-titles {
    width: 100%;
}
.page-id-181 .sg-titles h1,
        .page-id-181 .sg-titles h2,
        .page-id-181 .sg-titles h3,
        .page-id-181 .sg-titles h4,
        .page-id-181 .sg-titles h5,
        .page-id-181 .sg-titles h6 {
    width: 100%;
    margin-bottom: 20px;
}
.page-id-181 .sg-buttons {
    padding: 20px;
    clear: both;
    *zoom: 1;
}
.page-id-181 .sg-buttons:before {
    content: '';
    display: table;
}
.page-id-181 .sg-buttons:after {
    content: '';
    display: table;
    clear: both;
}
.page-id-181 .sg-buttons::before,
    .page-id-181 .sg-buttons::after {
    content: '';
    clear: both;
    display: block;
}
.page-id-181 .sg-buttons .button {
    margin-right: 20px;
}
.page-id-181 .sg-colors {
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-flex-wrap: wrap;
        -ms-flex-wrap: wrap;
            flex-wrap: wrap;
}
.page-id-181 .sg-colors li {
    -webkit-box-flex: 0;
    -webkit-flex: 0 0 auto;
        -ms-flex: 0 0 auto;
            flex: 0 0 auto;
    width: calc(99.99% * 1/4 - (30px - 30px * 1/4));
    clear: both;
    *zoom: 1;
    border-radius: 5px;
    overflow: hidden;
    box-shadow: 0 3px 0 $light_gray;
    margin-bottom: 30px;
}
.page-id-181 .sg-colors li:nth-child(1n) {
    margin-right: 30px;
    margin-left: 0;
}
.page-id-181 .sg-colors li:last-child {
    margin-right: 0;
}
.page-id-181 .sg-colors li:nth-child(4n) {
    margin-right: 0;
    margin-left: auto;
}
.page-id-181 .sg-colors li:before {
    content: '';
    display: table;
}
.page-id-181 .sg-colors li:after {
    content: '';
    display: table;
    clear: both;
}
.page-id-181 .sg-colors li::before,
    .page-id-181 .sg-colors li::after {
    content: '';
    clear: both;
    display: block;
}
.page-id-181 .sg-colors .sg-swatch {
    float: left;
    display: block;
    width: 50%;
    height: 75px;
}
.page-id-181 .sg-colors .sg-label {
    clear: both;
    display: block;
    float: none;
    font-weight: bold;
}
.page-id-181 .sg-colors .text {
    padding: 10px;
    clear: both;
    clear: both;
    *zoom: 1;
}
.page-id-181 .sg-colors .text:before {
    content: '';
    display: table;
}
.page-id-181 .sg-colors .text:after {
    content: '';
    display: table;
    clear: both;
}
.page-id-181 .sg-colors .text::before,
    .page-id-181 .sg-colors .text::after {
    content: '';
    clear: both;
    display: block;
}
.page-id-181 .sg-buttons .purple-bg {
    background: $purple;
    padding: 20px;
    display: inline-block;
}
.page-id-181 .sg-buttons .purple-bg .button {
    margin: 0;
}
.page-id-181 .form-w-text {
    margin-bottom: 60px;
}
